import { Suspense } from 'react'
import Header from '../components/Header'
import HeroCarousel from '../components/HeroCarousel'
import ProductCarousel from '../components/ProductCarousel'
import CategoryGrid from '../components/CategoryGrid'
import BrandUpdates from '../components/BrandUpdates'
import BeautyEvents from '../components/BeautyEvents'
import ManagementTips from '../components/ManagementTips'
import SurveyReports from '../components/SurveyReports'
import Footer from '../components/Footer'
import LoadingScreen from '../components/LoadingScreen'
import SearchBar from '../components/SearchBar'
import { pageSectionsAPI, articlesAPI, heroSlidesAPI } from '../src/lib/supabase'
import { heroSlides as fallbackHeroSlides, newProducts, categories, mostViewedProducts, mostViewedManufacturers } from '../constants'

// SSGのためのデータ取得
export const revalidate = 3600 // 1時間ごとに再生成

async function getPageData() {
  try {
    const [
      hotCosmeticsSection,
      popularMedicalBeautySection,
      brandUpdates,
      beautyEvents,
      managementTips,
      surveyReports,
      mostReadArticles,
      heroSlidesData
    ] = await Promise.all([
      pageSectionsAPI.getSectionByName('hot-cosmetics').catch(() => []),
      pageSectionsAPI.getSectionByName('popular-medical-beauty').catch(() => []),
      articlesAPI.getLatestArticles(6).catch(() => []),
      articlesAPI.getArticlesByCategory('イベント').catch(() => []),
      articlesAPI.getArticlesByCategory('肌育').catch(() => []),
      articlesAPI.getArticlesByCategory('レポート').catch(() => []),
      articlesAPI.getLatestArticles(10).catch(() => []),
      heroSlidesAPI.getAllSlides().catch(() => null)
    ])

    // Extract articles from sections
    const hotCosmetics = hotCosmeticsSection?.map((section: any) => section.article).filter(Boolean) || []
    const popularMedicalBeauty = popularMedicalBeautySection?.map((section: any) => section.article).filter(Boolean) || []

    // HeroSlide型の変換
    const heroSlides = heroSlidesData?.map(slide => ({
      id: slide.id.toString(),
      title: slide.article?.title || slide.alt_text || '',
      description: slide.article?.excerpt || '',
      imageUrl: slide.image_url,
      alt: slide.alt_text || '',
      articleId: slide.article_id,
      articleSlug: slide.article?.slug,
      linkUrl: slide.article ? `/article/${slide.article.slug || slide.article.id}` : '#',
      linkText: '詳細を見る',
      gradient: 'from-pink-500 to-rose-500'
    })) || fallbackHeroSlides

    return {
      hotCosmetics: hotCosmetics || [],
      popularMedicalBeauty: popularMedicalBeauty || [],
      brandUpdates: brandUpdates || [],
      beautyEvents: beautyEvents || [],
      managementTips: managementTips || [],
      surveyReports: surveyReports || [],
      mostReadArticles: mostReadArticles || [],
      heroSlides
    }
  } catch (error) {
    console.error('Failed to fetch page data:', error)
    return {
      hotCosmetics: [],
      popularMedicalBeauty: [],
      brandUpdates: [],
      beautyEvents: [],
      managementTips: [],
      surveyReports: [],
      mostReadArticles: [],
      heroSlides: fallbackHeroSlides
    }
  }
}

export default async function Home() {
  const pageData = await getPageData()

  return (
    <>
      <Header />
      <main className="min-h-screen bg-gray-50">
        <HeroCarousel slides={pageData.heroSlides} />
        <SearchBar />
        <CategoryGrid categories={categories} />
        <ProductCarousel
          title="注目のコスメ"
          products={newProducts}
          articles={pageData.hotCosmetics}
          mostRead={pageData.mostReadArticles}
        />
        <BrandUpdates
          articles={pageData.brandUpdates}
          products={mostViewedProducts}
          manufacturers={mostViewedManufacturers}
          popularMedicalBeauty={pageData.popularMedicalBeauty}
        />
        <BeautyEvents events={pageData.beautyEvents} />
        <ManagementTips articles={pageData.managementTips} />
        <SurveyReports articles={pageData.surveyReports} />
      </main>
      <Footer mostReadArticles={pageData.mostReadArticles} />
    </>
  )
}